___CSS_LOADER_EXPORT___.push([module.id, `/* Explore */
.explore {
  text-align: center;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 6rem;
  padding-top: 3em;
  color: var(--primary-color);
  text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.15);
}

/* Restaurants Grid */
.restaurants {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
  margin: 40px 0;
}

/* Restaurant Item */
.restaurant-item {
  width: 100%;
  border-radius: 16px;
  overflow: hidden;
  background-color: var(--card-bg);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.restaurant-item:hover {
  transform: translateY(-4px);
  box-shadow: 0 16px 32px rgba(0, 0, 0, 0.2);
}

.restaurant-item__header {
  position: relative;
  overflow: hidden;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__poster {
  width: 100%;
  height: 200px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.restaurant-item:hover
  .restaurant-item__header
  .restaurant-item__header__poster {
  transform: scale(1.05);
}

.restaurant-item .restaurant-item__header .restaurant-item__header__rating {
  position: absolute;
  padding: 8px;
  top: 15px;
  right: 15px;
  background-color: var(--primary-color);
  border-radius: 0;
  color: var(--text-light);
  font-weight: 600;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.restaurant-item .restaurant-item__header .restaurant-item__header__city {
  position: absolute;
  padding: 8px;
  top: 15px;
  left: 15px;
  background-color: var(--primary-color);
  border-radius: 0;
  color: var(--text-light);
  font-weight: 600;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.restaurant-item
  .restaurant-item__header
  .restaurant-item__header__rating
  .restaurant-item__header__rating__score {
  margin-right: 6px;
}

.restaurant-item .restaurant-item__content {
  padding: 20px;
  background-image: linear-gradient(
    to bottom,
    var(--card-bg),
    var(--dark-accent)
  );
  text-align: center;
}

.restaurant-item .restaurant-item__content h3 {
  margin: 0 0 8px 0;
  font-weight: 700;
  font-size: 1.6rem;
}

.restaurant-item .restaurant-item__content h3 a {
  color: var(--text-primary);
  text-decoration: none;
  display: inline-block;
  transition: color 0.3s ease;
}

.restaurant-item .restaurant-item__content h3 a:hover {
  color: var(--primary-color);
}

.restaurant-item .restaurant-item__content p {
  color: #6c757d;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-size: 0.9rem;
  line-height: 1.5;
  text-align: center;
}

/* Media Queries */
@media screen and (min-width: 650px) {
  .restaurants {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1200px) {
  .restaurants {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1600px) {
  .restaurants {
    grid-template-columns: repeat(4, 1fr);
  }
}
