___CSS_LOADER_EXPORT___.push([module.id, `/* Variables for consistent breakpoints */
:root {
  --breakpoint-tablet: 650px;
  --breakpoint-tablet-large: 800px;
  --breakpoint-desktop-small: 850px;
  --breakpoint-desktop: 1200px;
  --breakpoint-desktop-large: 1600px;
}

/* Tablet (650px and up) */
@media screen and (min-width: 650px) {
  /* App Bar Layout */
  .app-bar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
  }

  /* Brand/Logo */
  .app-bar__brand h1 {
    font-size: 1.5em;
  }

  /* Navigation */
  .app-bar__menu {
    display: none;
  }

  .app-bar__navigation {
    position: static;
    width: 100%;
  }

  .app-bar__navigation ul li {
    display: inline-block;
  }

  .app-bar__navigation ul li a {
    display: inline-block;
    width: 120px;
    text-align: center;
    margin: 0;
  }

  /* Restaurant Grid */
  .restaurants {
    grid-template-columns: repeat(2, 1fr);
  }

  /* Restaurant Detail Layout */
  .restaurant {
    grid-template-columns: auto 1fr;
  }

  .restaurant__title,
  .restaurant__overview {
    grid-column: 1 / -1; /* Spans full width */
  }
}

/* Large Tablet (800px and up) */
@media screen and (min-width: 800px) {
  .app-bar__brand h1 {
    font-size: 2em;
  }
}

/* Desktop (1200px and up) */
@media screen and (min-width: 1200px) {
  .restaurants {
    grid-template-columns: repeat(3, 1fr);
  }

  .restaurant__wrap {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
}

/* Large Desktop (1600px and up) */
@media screen and (min-width: 1600px) {
  .restaurants {
    grid-template-columns: repeat(4, 1fr);
  }
}
