___CSS_LOADER_EXPORT___.push([module.id, `/* Color Variables */
:root {
  --primary-color: #7895b2;
  --secondary-color: #e8dfca;
  --accent-color: #aebdca;
  --dark-accent: #f5efe6;
  --text-primary: #2c3333;
  --text-light: #ffffff;
  --shadow-color: rgba(0, 0, 0, 0.1);
  --card-bg: #ffffff;
  --menu-hover: #f8f9fa;
}

/* Base Styles with Overflow Protection */
html,
body {
  overflow-x: hidden;
  width: 100%;
  position: relative;
  padding: 0;
  margin: 0;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: var(--text-light);
  min-height: 100vh;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

/* Main Content */
main {
  padding: 32px;
  flex: 1;
  width: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}

.content {
  margin: 0 auto;
  min-height: 100%;
  width: 100%;
  overflow-x: hidden;
}

.content .content__heading {
  font-weight: normal;
}

/* Restaurant Detail */
.restaurant {
  margin: 2rem auto;
  width: calc(100% - 4rem);
  max-width: 1200px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  padding: 0 2rem;
  box-sizing: border-box;
  overflow: hidden;
}

.restaurant .restaurant__poster {
  width: 100%;
  max-width: 1000px;
  border-radius: 12px;
  margin: 0 auto;
  height: auto;
  object-fit: contain;
  padding-bottom: 12px;
}

/* Enhanced Restaurant Info Styles */
.restaurant__info {
  background-color: var(--card-bg);
  padding: 2.5rem;
  border-radius: 16px;
  box-shadow: 0 8px 24px var(--shadow-color);
  width: 100%;
  box-sizing: border-box;
  overflow-wrap: break-word;
  word-wrap: break-word;
  transition: all 0.3s ease;
  border: 1px solid rgba(0, 0, 0, 0.05);
  display: grid;
  gap: 1.5rem;
}

.restaurant__info:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.15);
}

.restaurant__info h4 {
  font-size: 1.2em;
  color: var(--text-primary);
  margin: 0;
  font-weight: 600;
  position: relative;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.restaurant__info h4::before {
  content: '';
  width: 4px;
  height: 1.2em;
  background: var(--primary-color);
  border-radius: 2px;
  margin-right: 0.5rem;
}

.restaurant__info p {
  font-size: 1.1em;
  color: var(--text-primary);
  margin: 0.5rem 0 0 1.5rem;
  line-height: 1.5;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid var(--secondary-color);
}

/* Rating Specific Styles */
.restaurant__info h4:last-of-type + p {
  font-size: 1.3em;
  font-weight: 600;
  color: #ffa41c;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

/* Info Section Hover Effects */
.restaurant__info > div {
  transition: all 0.2s ease;
  padding: 0.5rem;
  border-radius: 8px;
}

.restaurant__info > div:hover {
  background-color: var(--menu-hover);
}

.restaurant__title {
  text-align: center;
  font-size: 2.5em;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  position: relative;
  padding-bottom: 1rem;
  width: 100%;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.restaurant__title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background-color: var(--primary-color);
  border-radius: 2px;
}

.restaurant-label {
  font-size: 1.5em;
  color: var(--text-primary);
  font-weight: 600;
  margin: 2rem 0 1rem;
  padding-left: 1rem;
  border-left: 4px solid var(--primary-color);
  overflow-wrap: break-word;
  word-wrap: break-word;
}

/* Enhanced Menu Styles */
.menus {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1.5rem;
  padding: 1rem 0;
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

.menus li {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  height: auto;
  font-size: 1rem;
  line-height: 1.4;
  border-radius: 8px;
  background-color: var(--card-bg);
  box-shadow: 0 2px 8px var(--shadow-color);
  transition: all 0.3s ease;
  color: var(--text-primary);
  border: 1px solid var(--secondary-color);
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.menus li:hover {
  transform: translateY(-3px);
  box-shadow: 0 4px 12px var(--shadow-color);
  background-color: var(--menu-hover);
}

/* Enhanced Review Styles */
.review-section {
  background-color: var(--card-bg);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 12px var(--shadow-color);
  margin-top: 2rem;
  width: 100%;
  box-sizing: border-box;
}

/* Review Grid Container */
.review-boxs {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  padding: 1rem 0;
  list-style: none;
  margin: 0;
}

/* Individual Review Card */
.review {
  background-color: var(--dark-accent);
  padding: 1.25rem;
  border-radius: 8px;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  display: grid;
  grid-template-rows: auto auto 1fr;
  gap: 0.5rem;
  margin: 0;
  width: 100%;
}

.review:hover {
  transform: translateY(-3px);
  box-shadow: 0 4px 8px var(--shadow-color);
}

.review-label {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  padding-left: 1rem;
  border-left: 4px solid var(--primary-color);
}

.name-review {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.date-review {
  font-size: 0.85rem;
  color: #6c757d;
  margin: 0;
}

.description-review {
  font-size: 0.95rem;
  line-height: 1.5;
  color: var(--text-primary);
  margin: 0;
}

/* Enhanced Review Form */
#review-form {
  background-color: var(--card-bg);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 12px var(--shadow-color);
  width: 100%;
  max-width: 600px;
  margin: 2rem auto;
  border: none;
  box-sizing: border-box;
}

#review-form h3 {
  font-size: 1.8em;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 600;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

#review-form input[type='text'],
#review-form textarea {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid var(--secondary-color);
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  margin-bottom: 1rem;
  min-width: 44px;
  min-height: 44px;
  box-sizing: border-box;
}

#review-form input[type='text']:focus,
#review-form textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(120, 149, 178, 0.2);
}

#review-form button {
  background-color: var(--primary-color);
  color: var(--text-light);
  padding: 1rem 2rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  min-width: 44px;
  min-height: 44px;
  box-sizing: border-box;
}

#review-form button:hover {
  background-color: var(--accent-color);
  transform: translateY(-2px);
}

/* Restaurant Content Styles */
.restaurant__content {
  display: grid;
  gap: 1.5rem;
}

.restaurant__meta {
  display: flex;
  align-items: center;
  gap: 1rem;
  color: var(--text-secondary);
  font-size: 0.9em;
  margin-top: 1rem;
}

.restaurant__tag {
  display: inline-block;
  padding: 0.3em 0.8em;
  background-color: var(--primary-color);
  color: var(--text-light);
  border-radius: 20px;
  font-size: 0.85em;
  font-weight: 500;
  margin-right: 0.5rem;
}

/* Responsive Design */
@media screen and (min-width: 650px) {
  .restaurant {
    grid-template-columns: 1fr 2fr;
  }

  .restaurant .restaurant__title {
    grid-column: 1 / -1;
  }

  .restaurant .restaurant__overview {
    grid-column: 1 / -1;
  }

  .menus {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}

@media screen and (min-width: 900px) {
  .restaurant__wrap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    width: 100%;
    box-sizing: border-box;
  }
}

@media screen and (max-width: 768px) {
  .restaurant__info {
    padding: 2rem;
    gap: 1.2rem;
  }

  .restaurant__info h4 {
    font-size: 1.1em;
  }

  .restaurant__info p {
    font-size: 1em;
    margin: 0.4rem 0 0 1.2rem;
  }

  .review-boxs {
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 1rem;
  }

  .review {
    padding: 1rem;
  }

  .review-label {
    font-size: 1.3rem;
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 650px) {
  .restaurant {
    width: calc(100% - 2rem);
    padding: 0 1rem;
  }

  .restaurant .restaurant__info {
    padding: 1.5rem;
  }

  .restaurant__title {
    font-size: 2em;
  }

  .menus {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    gap: 1rem;
  }

  .menus li {
    padding: 0.8rem;
    font-size: 0.9rem;
  }

  .review-section {
    padding: 1.5rem;
  }

  .review {
    padding: 1.2rem;
  }

  #review-form {
    padding: 1.5rem;
  }

  #review-form h3 {
    font-size: 1.6em;
  }
}

@media screen and (max-width: 480px) {
  main {
    padding: 16px;
    width: 100%;
    box-sizing: border-box;
  }

  .restaurant {
    width: 100%;
    padding: 0 0.5rem;
  }

  .restaurant .restaurant__poster {
    max-width: 100%;
  }

  .restaurant__info {
    padding: 1.5rem;
    gap: 1rem;
  }

  .restaurant__info h4 {
    font-size: 1em;
  }

  .restaurant__info p {
    font-size: 0.95em;
    margin: 0.3rem 0 0 1rem;
  }

  .restaurant__info h4:last-of-type + p {
    font-size: 1.2em;
  }

  .menus {
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  }

  .menus li {
    padding: 0.6rem;
    font-size: 0.85rem;
  }

  .review-boxs {
    grid-template-columns: 1fr;
  }

  .review {
    padding: 0.875rem;
  }

  .name-review {
    font-size: 0.95rem;
  }

  .date-review {
    font-size: 0.8rem;
  }

  .description-review {
    font-size: 0.9rem;
  }

  #review-form input[type='text'],
  #review-form textarea {
    font-size: 0.9rem;
  }

  #review-form button {
    padding: 0.8rem 1.5rem;
    font-size: 0.9rem;
  }
}
