D,SAAS;EACX;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,iBAAiB;IACjB,mBAAmB;EACrB;AACF;;AAEA;EACE;IACE,wBAAwB;IACxB,eAAe;EACjB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,4DAA4D;IAC5D,SAAS;EACX;;EAEA;IACE,eAAe;IACf,iBAAiB;EACnB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,gBAAgB;EAClB;AACF;;AAEA;EACE;IACE,aAAa;IACb,WAAW;IACX,sBAAsB;EACxB;;EAEA;IACE,WAAW;IACX,iBAAiB;EACnB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;IACf,SAAS;EACX;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,iBAAiB;IACjB,uBAAuB;EACzB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,4DAA4D;EAC9D;;EAEA;IACE,eAAe;IACf,kBAAkB;EACpB;;EAEA;IACE,0BAA0B;EAC5B;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,kBAAkB;EACpB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;;IAEE,iBAAiB;EACnB;;EAEA;IACE,sBAAsB;IACtB,iBAAiB;EACnB;AACF","sourcesContent":["/* Color Variables */\r\n:root {\r\n  --primary-color: #7895b2;\r\n  --secondary-color: #e8dfca;\r\n  --accent-color: #aebdca;\r\n  --dark-accent: #f5efe6;\r\n  --text-primary: #2c3333;\r\n  --text-light: #ffffff;\r\n  --shadow-color: rgba(0, 0, 0, 0.1);\r\n  --card-bg: #ffffff;\r\n  --menu-hover: #f8f9fa;\r\n}\r\n\r\n/* Base Styles with Overflow Protection */\r\nhtml,\r\nbody {\r\n  overflow-x: hidden;\r\n  width: 100%;\r\n  position: relative;\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\n* {\r\n  padding: 0;\r\n  margin: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n  font-family: 'Poppins', sans-serif;\r\n  background-color: var(--text-light);\r\n  min-height: 100vh;\r\n}\r\n\r\nimg {\r\n  display: block;\r\n  max-width: 100%;\r\n  height: auto;\r\n  object-fit: contain;\r\n}\r\n\r\n/* Main Content */\r\nmain {\r\n  padding: 32px;\r\n  flex: 1;\r\n  width: 100%;\r\n  box-sizing: border-box;\r\n  overflow-x: hidden;\r\n}\r\n\r\n.content {\r\n  margin: 0 auto;\r\n  min-height: 100%;\r\n  width: 100%;\r\n  overflow-x: hidden;\r\n}\r\n\r\n.content .content__heading {\r\n  font-weight: normal;\r\n}\r\n\r\n/* Restaurant Detail */\r\n.restaurant {\r\n  margin: 2rem auto;\r\n  width: calc(100% - 4rem);\r\n  max-width: 1200px;\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 2rem;\r\n  padding: 0 2rem;\r\n  box-sizing: border-box;\r\n  overflow: hidden;\r\n}\r\n\r\n.restaurant .restaurant__poster {\r\n  width: 100%;\r\n  max-width: 1000px;\r\n  border-radius: 12px;\r\n  margin: 0 auto;\r\n  height: auto;\r\n  object-fit: contain;\r\n  padding-bottom: 12px;\r\n}\r\n\r\n/* Enhanced Restaurant Info Styles */\r\n.restaurant__info {\r\n  background-color: var(--card-bg);\r\n  padding: 2.5rem;\r\n  border-radius: 16px;\r\n  box-shadow: 0 8px 24px var(--shadow-color);\r\n  width: 100%;\r\n  box-sizing: border-box;\r\n  overflow-wrap: break-word;\r\n  word-wrap: break-word;\r\n  transition: all 0.3s ease;\r\n  border: 1px solid rgba(0, 0, 0, 0.05);\r\n  display: grid;\r\n  gap: 1.5rem;\r\n}\r\n\r\n.restaurant__info:hover {\r\n  transform: translateY(-4px);\r\n  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.15);\r\n}\r\n\r\n.restaurant__info h4 {\r\n  font-size: 1.2em;\r\n  color: var(--text-primary);\r\n  margin: 0;\r\n  font-weight: 600;\r\n  position: relative;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.restaurant__info h4::before {\r\n  content: '';\r\n  width: 4px;\r\n  height: 1.2em;\r\n  background: var(--primary-color);\r\n  border-radius: 2px;\r\n  margin-right: 0.5rem;\r\n}\r\n\r\n.restaurant__info p {\r\n  font-size: 1.1em;\r\n  color: var(--text-primary);\r\n  margin: 0.5rem 0 0 1.5rem;\r\n  line-height: 1.5;\r\n  padding-bottom: 0.5rem;\r\n  border-bottom: 1px solid var(--secondary-color);\r\n}\r\n\r\n/* Rating Specific Styles */\r\n.restaurant__info h4:last-of-type + p {\r\n  font-size: 1.3em;\r\n  font-weight: 600;\r\n  color: #ffa41c;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n}\r\n\r\n/* Info Section Hover Effects */\r\n.restaurant__info > div {\r\n  transition: all 0.2s ease;\r\n  padding: 0.5rem;\r\n  border-radius: 8px;\r\n}\r\n\r\n.restaurant__info > div:hover {\r\n  background-color: var(--menu-hover);\r\n}\r\n\r\n.restaurant__title {\r\n  text-align: center;\r\n  font-size: 2.5em;\r\n  font-weight: 700;\r\n  color: var(--text-primary);\r\n  margin-bottom: 1.5rem;\r\n  position: relative;\r\n  padding-bottom: 1rem;\r\n  width: 100%;\r\n  overflow-wrap: break-word;\r\n  word-wrap: break-word;\r\n}\r\n\r\n.restaurant__title::after {\r\n  content: '';\r\n  position: absolute;\r\n  bottom: 0;\r\n  left: 50%;\r\n  transform: translateX(-50%);\r\n  width: 80px;\r\n  height: 3px;\r\n  background-color: var(--primary-color);\r\n  border-radius: 2px;\r\n}\r\n\r\n.restaurant-label {\r\n  font-size: 1.5em;\r\n  color: var(--text-primary);\r\n  font-weight: 600;\r\n  margin: 2rem 0 1rem;\r\n  padding-left: 1rem;\r\n  border-left: 4px solid var(--primary-color);\r\n  overflow-wrap: break-word;\r\n  word-wrap: break-word;\r\n}\r\n\r\n/* Enhanced Menu Styles */\r\n.menus {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));\r\n  gap: 1.5rem;\r\n  padding: 1rem 0;\r\n  width: 100%;\r\n  overflow: hidden;\r\n  box-sizing: border-box;\r\n}\r\n\r\n.menus li {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  padding: 1rem;\r\n  height: auto;\r\n  font-size: 1rem;\r\n  line-height: 1.4;\r\n  border-radius: 8px;\r\n  background-color: var(--card-bg);\r\n  box-shadow: 0 2px 8px var(--shadow-color);\r\n  transition: all 0.3s ease;\r\n  color: var(--text-primary);\r\n  border: 1px solid var(--secondary-color);\r\n  overflow-wrap: break-word;\r\n  word-wrap: break-word;\r\n}\r\n\r\n.menus li:hover {\r\n  transform: translateY(-3px);\r\n  box-shadow: 0 4px 12px var(--shadow-color);\r\n  background-color: var(--menu-hover);\r\n}\r\n\r\n/* Enhanced Review Styles */\r\n.review-section {\r\n  background-color: var(--card-bg);\r\n  padding: 2rem;\r\n  border-radius: 12px;\r\n  box-shadow: 0 4px 12px var(--shadow-color);\r\n  margin-top: 2rem;\r\n  width: 100%;\r\n  box-sizing: border-box;\r\n}\r\n\r\n/* Review Grid Container */\r\n.review-boxs {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));\r\n  gap: 1.5rem;\r\n  padding: 1rem 0;\r\n  list-style: none;\r\n  margin: 0;\r\n}\r\n\r\n/* Individual Review Card */\r\n.review {\r\n  background-color: var(--dark-accent);\r\n  padding: 1.25rem;\r\n  border-radius: 8px;\r\n  transition: transform 0.2s ease, box-shadow 0.2s ease;\r\n  display: grid;\r\n  grid-template-rows: auto auto 1fr;\r\n  gap: 0.5rem;\r\n  margin: 0;\r\n  width: 100%;\r\n}\r\n\r\n.review:hover {\r\n  transform: translateY(-3px);\r\n  box-shadow: 0 4px 8px var(--shadow-color);\r\n}\r\n\r\n.review-label {\r\n  font-size: 1.5rem;\r\n  font-weight: 600;\r\n  color: var(--text-primary);\r\n  margin-bottom: 1.5rem;\r\n  padding-left: 1rem;\r\n  border-left: 4px solid var(--primary-color);\r\n}\r\n\r\n.name-review {\r\n  font-size: 1rem;\r\n  font-weight: 600;\r\n  color: var(--text-primary);\r\n  margin: 0;\r\n}\r\n\r\n.date-review {\r\n  font-size: 0.85rem;\r\n  color: #6c757d;\r\n  margin: 0;\r\n}\r\n\r\n.description-review {\r\n  font-size: 0.95rem;\r\n  line-height: 1.5;\r\n  color: var(--text-primary);\r\n  margin: 0;\r\n}\r\n\r\n/* Enhanced Review Form */\r\n#review-form {\r\n  background-color: var(--card-bg);\r\n  padding: 2rem;\r\n  border-radius: 12px;\r\n  box-shadow: 0 4px 12px var(--shadow-color);\r\n  width: 100%;\r\n  max-width: 600px;\r\n  margin: 2rem auto;\r\n  border: none;\r\n  box-sizing: border-box;\r\n}\r\n\r\n#review-form h3 {\r\n  font-size: 1.8em;\r\n  color: var(--text-primary);\r\n  margin-bottom: 1.5rem;\r\n  text-align: center;\r\n  font-weight: 600;\r\n  overflow-wrap: break-word;\r\n  word-wrap: break-word;\r\n}\r\n\r\n#review-form input[type='text'],\r\n#review-form textarea {\r\n  width: 100%;\r\n  padding: 0.8rem 1rem;\r\n  border: 1px solid var(--secondary-color);\r\n  border-radius: 8px;\r\n  font-size: 1rem;\r\n  transition: all 0.3s ease;\r\n  margin-bottom: 1rem;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n  box-sizing: border-box;\r\n}\r\n\r\n#review-form input[type='text']:focus,\r\n#review-form textarea:focus {\r\n  outline: none;\r\n  border-color: var(--primary-color);\r\n  box-shadow: 0 0 0 3px rgba(120, 149, 178, 0.2);\r\n}\r\n\r\n#review-form button {\r\n  background-color: var(--primary-color);\r\n  color: var(--text-light);\r\n  padding: 1rem 2rem;\r\n  border: none;\r\n  border-radius: 8px;\r\n  font-size: 1rem;\r\n  font-weight: 600;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  width: 100%;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n  box-sizing: border-box;\r\n}\r\n\r\n#review-form button:hover {\r\n  background-color: var(--accent-color);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n/* Restaurant Content Styles */\r\n.restaurant__content {\r\n  display: grid;\r\n  gap: 1.5rem;\r\n}\r\n\r\n.restaurant__meta {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n  color: var(--text-secondary);\r\n  font-size: 0.9em;\r\n  margin-top: 1rem;\r\n}\r\n\r\n.restaurant__tag {\r\n  display: inline-block;\r\n  padding: 0.3em 0.8em;\r\n  background-color: var(--primary-color);\r\n  color: var(--text-light);\r\n  border-radius: 20px;\r\n  font-size: 0.85em;\r\n  font-weight: 500;\r\n  margin-right: 0.5rem;\r\n}\r\n\r\n/* Responsive Design */\r\n@media screen and (min-width: 650px) {\r\n  .restaurant {\r\n    grid-template-columns: 1fr 2fr;\r\n  }\r\n\r\n  .restaurant .restaurant__title {\r\n    grid-column: 1 / -1;\r\n  }\r\n\r\n  .restaurant .restaurant__overview {\r\n    grid-column: 1 / -1;\r\n  }\r\n\r\n  .menus {\r\n    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 900px) {\r\n  .restaurant__wrap {\r\n    display: grid;\r\n    grid-template-columns: repeat(2, 1fr);\r\n    gap: 2rem;\r\n    width: 100%;\r\n    box-sizing: border-box;\r\n  }\r\n}\r\n\r\n@media screen and (max-width: 768px) {\r\n  .restaurant__info {\r\